.classTurnComplete {
  grid-column: turn-complete;
  margin-right: 10px;
}

.classInfo {
  grid-column: info;
}

.classInfo .classInfoContainer {
  display: table;
  font-size: large;
}

.classInfo .classInfoContainer.summonInfoContainer {
  font-size: small;
  min-height: 20px;
}

.classInfo .classInfoContainer .className {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}

.classInfo .classInfoContainer .classIcon {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  padding-right: 6px;
  width: 18px;
  height: 18px;
}

.classInfo .classInfoContainer .classIcon img {
  width: 18px;
  height: 18px;
}

.classSeparator {
  grid-column: 1/-1;
  height: 8px;
}

.classInit {
  grid-column: initiative;
  font-size: x-large;
  margin-left: 20px;
}

.classMoveButtons {
  grid-column: move-buttons;
  margin-left: 4px;
}

.classMoveButtons .classMoveButtonContainer {
  position: relative;
  width: 18px;
}

.classMoveButtons .classMoveButtonContainer .moveUp,
.classMoveButtons .classMoveButtonContainer .moveDown {
  position: absolute;
  width: 0px;
  height: 0px;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  display: inline-block;
}

.classMoveButtons .classMoveButtonContainer .moveUp {
  top: -11px;
  border-bottom: 9px solid #646464;
}

.classMoveButtons .classMoveButtonContainer .moveDown {
  top: 2px;
  border-top: 9px solid #646464;
}

.classDelete {
  grid-column: delete;
  margin-left: 8px;
  color: #646464;
}
